.search-car-slider-section {
    margin-bottom: 60px;
    position: relative;
    .filter-main-wrap {
        position: absolute;
        top: 45%;
        left: 50%;
        width: 100%;
        transform: translate(-50%, -50%);
        @include media-breakpoint-down(sm) {
            top: 50%;
        }
        .filter-box {
            padding: 40px 40px 25px;
            box-shadow: 0px 3px 6px rgba($black, 0.16);
            border-radius: 17px;
            background-color: $grey-1;
            @include media-breakpoint-down(sm) {
                padding: 40px 40px 40px;
            }
            p {
                font-weight: $semi-bold;
                margin-bottom: 35px;
            }
            .form-group {
                margin-bottom: 25px;
                @include media-breakpoint-down(sm) {
                    margin-bottom: 0;
                }
                .home_filter_box {
                    @include media-breakpoint-down(sm) {
                        margin-bottom: 25px;
                    }
                }
            }
            .form-control {
                font-size: 12px;
                color: $accent;
                height: 42px;
            }
            label {
                font-size: 12px;
                color: $accent;
            }
            .btn {
                background-color: $primary;
                color: $white;
                width: 100%;
                padding: 11px 15px;
                font-size: 10px;
                &:hover {
                    background-color: $grey-7;
                }
            }
        }
    }
    .car-value {
        padding: 30px 5px;
        border-radius: 17px;
        background-color: $primary;
        margin-top: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        color: $white;
        text-align: center;
        .car-value-list {
            padding: 0 25px;
            border-right: 1px solid $white;
            &:last-child {
                border-right: 0;
            }
        }
        i,
        img {
            margin-bottom: 18px;
        }
        img {
            height: 25px;
            width: auto;
        }
        i {
            font-size: 2.2rem;
        }
        span {
            display: block;
            font-size: 1.8rem;
            font-weight: $semi-bold;
        }
    }
    .slider-col {
        @include media-breakpoint-down(md) {
            margin-top: 30px;
        }
    }
    .home-slider {
        img {
            width: 100%;
            height: 560px;
            object-fit: cover;
            box-shadow: 0px 3px 6px rgba($black, 0.16);
            @include media-breakpoint-down(sm) {
                height: 620px;
            }
        }
        .slick-dots {
            bottom: 50px;
            text-align: right;
            width: 100%;
            right: -25%;
            transform: translate(-50%, -50%);
            @include media-breakpoint-down(sm) {
                display: none !important;
            }
            li {
                height: auto;
                width: auto;
                button {
                    width: 80px;
                    padding: 0;
                    background-color: $grey-2;
                    height: 8px;
                    border-radius: 4px;
                    &:before {
                        content: none;
                    }
                }
                &.slick-active {
                    button {
                        background-color: $primary;
                    }
                }
            }
        }
    }
}

.featured-product-section {
    margin: 40px 0;
    &.grey-bg {
        padding: 40px 0;
    }
    .featured-product-slider {
        //margin-left: -10px;
        //margin-right: -10px;
        display: flex;
        flex-wrap: nowrap;
        padding-bottom: 40px;
        .featured-product-slider-item {
            width: 33.3%;
            padding: 0 8px;
            flex: 0 0 auto;
            @include media-breakpoint-down(md) {
                width: 50%;
            }
            @include media-breakpoint-down(sm) {
                width: 100%;
            }
        }
        .slick-slide {
            padding-left: 10px;
            padding-right: 10px;
            margin-bottom: 15px;
            height: auto;
            > div {
                height: 100%;
                .featured-product-slider-item {
                    height: 100%;
                }
            }
        }
        .slick-track {
            display: flex;
        }
    }
}

.card-icon-section {
    background-color: $grey-1;
    padding: 60px 0;
    @include media-breakpoint-down(md) {
        padding: 40px 0;
    }
}

.recent-articles-section {
    margin: 40px 0;
}

.recent-videos-section {
    background-color: $grey-1;
    margin: 60px 0;
    padding: 40px 0;
}

.car_form_home a {
    @include media-breakpoint-down(sm) {
        text-align: center;
        display: block;
        margin-bottom: 15px;
    }
}
