.about-main-wrap {
    padding: 80px 0 80px;
    @include media-breakpoint-down(sm) {
        padding: 40px 0 40px;
    }
    .about-img-wrap {
        text-align: center;
        @include media-breakpoint-down(sm) {
            margin-top: 20px;
        }
        p {
            font-size: 18px;
            font-weight: bold;
            margin-bottom: 80px;
            color: #484848;
            @include media-breakpoint-down(sm) {
                margin-bottom: 50px;
            }
        }
    }
    h1 {
        margin-bottom: 50px;
        @include media-breakpoint-down(sm) {
            margin-bottom: 30px;
        }
    }
    span {
        color: $primary;
    }
    p {
        margin-bottom: 25px;
        font-size: 12px;
        @include media-breakpoint-down(sm) {
            margin-bottom: 15px;
        }
        strong {
            font-size: 14px;
        }
    }
    ul {
        font-weight: 600;
        margin: 0;
        padding: 0;
        list-style: none;
        font-size: 12px;
        li {
            margin-bottom: 20px;
            position: relative;
            padding-left: 20px;
            &::before {
                content: url("../images/power-icon.png");
                position: absolute;
                top: 2px;
                left: 0;
            }
        }
    }
}

.inventary-main-wrap {
    background-image: url("../images/dark-bg.jpg");
    background-size: cover;
    padding: 100px 0;
    @include media-breakpoint-down(sm) {
        padding: 60px 0;
    }
    h2 {
        text-align: center;
        color: $white;
        margin-bottom: 60px;
    }
    .ginput_container.ginput_container_checkbox {
        text-align: center;
        @include media-breakpoint-down(sm) {
            text-align: left;
        }
    }
    .gform_wrapper .gfield-choice-input + label {
        color: $white;
        @include media-breakpoint-down(sm) {
            font-size: 12px !important;
        }
    }
    .gform_footer.top_label {
        text-align: center;
    }
    .gform_button {
        border: none;
    }
    .gform_body.gform-body {
        position: relative;
    }
    .message-field {
        position: absolute;
        top: 0;
        width: 48%;
        @include media-breakpoint-down(sm) {
            position: static;
            width: 100%;
        }
    }
    .gfield.gfield--width-half {
        margin-bottom: 15px;
        @include media-breakpoint-down(sm) {
            margin-bottom: 0;
        }
    }
    .gform_wrapper {
        input {
            height: 48px;
        }
    }
}
