/*BUTTON CSS*/

.btn {
    @include transition(all 0.3s ease-in-out);
    font-weight: 500;
    padding: 0.5rem 1.5rem;
    position: relative;
    text-transform: uppercase;
    border-radius: 25px;
    font-size: 1rem;
    border: 2px solid transparent;
    @include media-breakpoint-down(sm) {
        padding: 0.5rem 1rem;
    }
    &:hover,
    &:focus {
        outline: none;
    }
}

.btn-white{
    background-color: $white;
    color: $primary;
    border-color: $white;
    &:hover,
    &:focus {
        color: $white;
        background-color: $primary;
        border-color: $primary;
    }
}

.btn-green{
    background-color: $primary;
    color: $white;
    border-color: $primary;
    &:hover,
    &:focus {
        color: $white;
        background-color: $secondary;
        border-color: $secondary;
    }
}

.btn-yellow{
    background-color: $secondary;
    color: $primary;
    border-color: $secondary;
    &:hover,
    &:focus {
        color: $primary;
        background-color: $white;
        border-color: $white;
    }
}


.border-btn {
    background: transparent;
    color: $primary;
    border: 2px solid $primary;
    &:hover,
    &:focus {
        color: $white;
        background-color: $primary;
        border-color: $primary;
    }
}