.single,
.archive {
    .wrapper {
        padding-bottom: 80px;
        @include media-breakpoint-down(sm) {
            padding-bottom: 40px;
        }
    }
    .content-area {
        h1,
        h2,
        h3,
        h4,
        h5 {
            margin-bottom: 10px;
        }
        img {
            width: 100%;
            height: auto;
            border-bottom: 3px solid $primary;
            padding-bottom: 10px;
        }
        .entry-content {
            margin: 20px 0;
            iframe {
                width: 100%;
            }
        }
        .entry-header {
            padding-top: 20px;
        }
        .entry-meta {
            margin-bottom: 15px;
        }
    }
    .widget-area {
        @include media-breakpoint-down(sm) {
            margin-top: 30px;
        }
        .widget_block {
            margin-bottom: 30px;
        }
        h2 {
            color: $primary;
            margin-bottom: 30px;
            font-size: 2rem;
        }
        ul {
            padding: 0;
            margin: 0;
            list-style: none;
            li {
                display: flex;
                align-items: flex-start;
                position: relative;
                padding-left: 25px;
                padding-bottom: 20px;
                &::before {
                    position: absolute;
                    content: "\f101";
                    left: 2px;
                    top: 1px;
                    font: normal normal normal 2rem/1 FontAwesome;
                    color: $accent;
                    @include media-breakpoint-down(md) {
                        font: normal normal normal 1rem/1 FontAwesome;
                    }
                }
                a {
                    color: $accent;
                    text-decoration: underline;
                    p {
                        font-size: 1.125rem;
                        line-height: 1.4;
                        margin: 0;
                        @include media-breakpoint-down(md) {
                            font-size: 1rem;
                        }
                    }
                }
                &:hover {
                    a {
                        color: $primary;
                    }
                    &::before {
                        color: $primary;
                    }
                }
            }
        }
    }
    .wp-block-search__button {
        background-color: $primary;
        color: $white;
        padding: 15px 25px;
        &:hover {
            background-color: $grey-7;
        }
    }
    .navigation {
        span {
            a {
                font-weight: bold;
                i {
                    &::before {
                        font-weight: bold;
                        font-size: 18px;
                    }
                    &::after {
                        font-weight: bold;
                        font-size: 18px;
                    }
                }
            }
        }
    }
}
