/*Common CSS START*/

html,
body {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    font-size: 62.5%;
    font-family: $global-font;
    color: $accent;
}

body {
    font-size: 1.6rem;
    line-height: 1.5;
}

img {
    max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: $semi-bold;
}

h1 {
    font-size: 3.6rem; //36px
    @include media-breakpoint-down(md) {
        font-size: 3.2rem; //32px
    }
    @include media-breakpoint-down(sm) {
        font-size: 2.8rem; //28px
    }
}

h2 {
    font-size: 2.8rem; //28px
    @include media-breakpoint-down(md) {
        font-size: 2.6rem; //26px
    }
    @include media-breakpoint-down(sm) {
        font-size: 2.4rem; //24px
    }
}

h3 {
    font-size: 2.6rem; //26px
    @include media-breakpoint-down(md) {
        font-size: 2.4rem; //24px
    }
    @include media-breakpoint-down(sm) {
        font-size: 2.2rem; //22px
    }
}

h4 {
    font-size: 2.4rem; //24px
    @include media-breakpoint-down(md) {
        font-size: 2.2rem; //22px
    }
    @include media-breakpoint-down(sm) {
        font-size: 2rem; //20px
    }
}

h5 {
    font-size: 1.7rem; //16px
}
h6 {
    font-size: 1.4rem; //16px
}
strong {
    font-weight: $bold;
}

a {
    cursor: pointer;
    text-decoration: none;
    @include transition(all 0.4s ease-in-out);
    &:hover,
    &:focus {
        outline: none;
        text-decoration: none;
    }
}

iframe {
    max-width: 100%;
}

.more-link {
    margin-top: 40px;
    display: flex;
}
.link-with-arrow {
    color: $black;
    font-weight: $semi-bold;
    &:hover {
        color: $primary;
    }
    i {
        margin-left: 10px;
        color: $primary;
    }
}

.grey-bg {
    background-color: $grey-1;
}

input[type="range"] {
    -webkit-appearance: none;
    width: 100%;
    height: 5px;
    border-radius: 5px;
    background: #f2f2f2;
    outline: none;
    opacity: 1;
}

input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 19px;
    height: 19px;
    border-radius: 50%;
    background: $primary;
    cursor: pointer;
    box-shadow: 0px 5px 8px rgba($black, 0.16);
    border: 2px solid $primary;
}

input[type="range"]::-moz-range-thumb {
    width: 19px;
    height: 19px;
    border-radius: 50%;
    background: $primary;
    cursor: pointer;
    box-shadow: 0px 5px 8px rgba($black, 0.16);
    border: 2px solid $primary;
}
.gform_wrapper {
    input {
        font-size: 12px;
        font-weight: 600;
        border: 0.5px solid $grey-6;
        border-radius: 7px;
        &:focus {
            outline: 1px solid $primary;
        }
    }
    textarea {
        font-size: 12px;
        font-weight: 600;
        border: 0.5px solid $grey-6;
        border-radius: 7px;
        &:focus {
            outline: 1px solid $primary;
        }
    }
    .gfield-choice-input + label {
        font-size: 12px;
        color: #666666;
    }
    ::-webkit-input-placeholder {
        color: $grey-6;
    }
    :-moz-placeholder {
        opacity: 1;
        color: $grey-6;
    } /* Firefox 18- */
    ::-moz-placeholder {
        opacity: 1;
        color: $grey-6;
    } /* firefox 19+ */
    :-ms-input-placeholder {
        color: $grey-6;
    } /* ie */
    input:-moz-placeholder {
        color: $grey-6;
    }
    .gform_button {
        background-color: $primary;
        color: $white;
        padding: 13px 35px;
        font-size: 12px;
        border-radius: 21px;
        text-align: center;
        &:hover {
            background-color: $grey-7;
        }
    }
}

.login-register {
    padding-bottom: 50px;
    h4 {
        margin-bottom: 15px;
    }
    .ginput_container {
        input[type="text"] {
            width: 100%;
        }
        input[type="password"] {
            width: 100%;
        }
    }
    .gf_login_links {
        display: flex;
        justify-content: space-between;
    }
}

.simplebar-track.simplebar-horizontal {
    height: 8px;
    background-color: #f6f8f9;
}
.simplebar-track.simplebar-horizontal .simplebar-scrollbar {
    top: 0;
    height: 8px;
}
.simplebar-scrollbar:before {
    background: $primary;
}
.simplebar-scrollbar.simplebar-visible:before {
    opacity: 1;
}

.btn-default {
    background-color: $primary;
    color: $white;
    padding: 13px 40px;
    text-align: center;
    border-radius: 25px;
    font-size: 10px;
    text-transform: uppercase;
    display: inline-block;
    &:hover {
        color: $white;
        background-color: $grey-7;
    }
}

@media (min-width: 1400px) {
    .container,
    .container-lg,
    .container-md,
    .container-sm,
    .container-xl,
    .container-xxl {
        max-width: 1430px;
    }
}

.text-budge {
    position: absolute;
    top: 0;
    left: 5%;
    padding: 10px;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    background-color: $primary;
    color: $white;
    font-size: 12px;
    z-index: 1;
    box-shadow: 0px 3px 6px rgba($black, 0.16);
    max-width: 90px;
    width: 100%;
    text-align: center;
}

select {
    appearance: none;
    background-image: url(../images/arrow-back.svg);
    background-repeat: no-repeat;
    background-position: 96% center;
    background-size: 13px;
}

.news_load_more_btn {
    position: absolute;
    bottom: 0;
    z-index: 111;
    left: 50%;
}
section.news-tabs-wrap .row.card-content-row {
    position: relative;
    padding-bottom: 50px;
}
section.recent-videos-section .row.card-content-row {
    position: relative;
    padding-bottom: 50px;
}
.compare-main-wrap .compare-car .carModelBtn .car-icon-wrap img {
    opacity: 0.5;
}
.compare-main-wrap .compare-car .carModelBtn.active .car-icon-wrap img {
    opacity: 1;
}

.feature-box {
    display: none;
}
ul.pagination li.active span {
    color: #0d46ff;
}
ul.pagination a.car_list_load_more_btn,
ul.pagination span {
    padding: 0px 7px;
    position: static;
    background: transparent;
    color: #000;
    font-size: 22px;
}
ul.pagination {
    width: 100%;
    justify-content: end;
}
ul.pagination a.car_list_load_more_btn.gray {
    color: gray;
}
ul.pagination a.car_list_load_more_btn.gray i {
    font-weight: 400;
    margin: 0px -5px;
}
.feature-box-style {
    font-size: 12px;
    color: #000;
    border: 0.5px solid #ccc;
    padding: 14px 8px; /* color: aliceblue; */
}

.feature-box-style {
}

.feature-box-style a.feature-box-btn {
    color: #000000ba;
}

.feature-box {
    margin-top: 13px;
}

.loading_posts {
    width: 100%;
    text-align: center;
}

/* home Page Banner Css */

@media (min-width: 1200px) {
    .slider_image_tab {
        display: none !important;
    }
    .slider_image_mobile {
        display: none !important;
    }
}
@media (max-width: 1200px) {
    .slider_image {
        display: none !important;
    }
    .slider_image_tab {
        display: block !important;
    }
    .slider_image_mobile {
        display: none !important;
    }
}
@media (max-width: 767px) {
    .slider_image {
        display: none !important;
    }
    .slider_image_tab {
        display: none !important;
    }
    .slider_image_mobile {
        display: block !important;
    }
}
