.listing-main-wrap {
    padding-bottom: 50px;
    @include media-breakpoint-down(md) {
        padding-bottom: 90px;
    }
    .sort-filter-wrap {
        margin-bottom: 50px !important;
        margin-top: -102px;
        @include media-breakpoint-down(md) {
            margin-top: 0;
            margin-bottom: 40px !important;
        }
        .form-group {
            @include media-breakpoint-down(sm) {
                display: block !important;
                text-align: center;
            }
        }
        label {
            @include media-breakpoint-down(sm) {
                margin-bottom: 15px !important;
            }
        }
        select {
            margin-left: 10px !important;
            @include media-breakpoint-down(sm) {
                margin-left: 0 !important;
            }
        }
    }
    .filter-siderbar {
        box-shadow: 0px 5px 8px rgba($black, 0.16);
        font-size: 12px;
        .btn {
            background-color: $primary;
            color: $white;
            width: 100%;
            padding: 11px 15px;
            font-weight: 600;
            &:hover {
                background-color: $grey-7;
            }
        }
        .filter-title {
            display: flex;
            background-color: $grey-7;
            color: $white;
            padding: 50px 20px;
            align-items: center;
            justify-content: center;
            h4 {
                font-size: 18px;
                margin-left: 15px;
                margin-bottom: 0;
            }
        }
        .filter_listing_form {
            padding: 40px 30px;
            margin-bottom: 50px;
            .filter_box {
                margin-bottom: 20px;
            }
            .round-box {
                border: 0.5px solid $grey-6;
                border-radius: 5px;
                padding: 15px;
            }
        }
    }
    .filter_box {
        .form-group {
            display: flex;
            align-items: center;
            label {
                min-width: 70px;
                margin: 0;
                font-size: 12px;
                text-transform: uppercase;
            }
            select {
                margin: 0;
            }
        }
        .form-control {
            font-size: 12px;
            color: $accent;
            height: 42px;
            border: 0.5px solid $grey-6;
        }
        select {
            border-radius: 7px;
            font-weight: $semi-bold;
            margin-bottom: 20px;
        }
        .form-check-label {
            margin-left: 8px;
        }
        .flex_class {
            display: flex;
            justify-content: space-between;
        }
        label {
            font-weight: 600;
        }
        .form-check {
            label {
                font-weight: normal;
            }
        }
        .slide-filter {
            border: 0.5px solid $grey-6;
            border-radius: 7px;
            padding: 10px;
        }
    }
    .image-card {
        margin-bottom: 35px;
        height: auto;
        figure {
            height: 223px;
        }
        .image-card-detail {
            .product-price {
                justify-content: flex-end;
            }
            .image-card-detail-row {
                display: block;
                .card-left-detail {
                    max-width: 100%;
                    flex: 0 0 100%;
                    h5 {
                        max-width: 100%;
                    }
                }
                .card-right-detail {
                    flex: 0 0 100%;
                    max-width: 100%;
                    padding-left: 0;
                    flex-wrap: inherit;
                    width: 100%;
                    margin-bottom: 25px;
                    display: flex;
                    .card-detail-list {
                        display: flex;
                        align-items: center;
                        margin-bottom: 0;
                        margin-right: 20px;
                        .card-detail-icon {
                            margin-right: 10px;
                            width: 25px;
                            min-width: 25px;
                        }
                    }
                }
            }
        }
    }
}

#filter-data {
    position: relative;
}

.car_list_load_more_btn {
    position: absolute;
    left: 50%;
    bottom: -100px;
    background-color: $primary;
    color: $white;
    width: 100%;
    padding: 13px 15px;
    max-width: 175px;
    text-align: center;
    border-radius: 25px;
    margin: 0 auto;
    transform: translate(-50%, -50%);
    font-size: 10px;
    text-transform: uppercase;
    @include media-breakpoint-down(md) {
        bottom: -75px;
    }
    &:hover {
        color: $white;
        background-color: $grey-7;
    }
}

.dealer_load_more_btn {
    background-color: $primary;
    color: $white;
    width: 100%;
    padding: 13px 15px;
    max-width: 175px;
    text-align: center;
    border-radius: 25px;
    margin: 0 auto;
    font-size: 10px;
    text-transform: uppercase;
    display: block;
    @include media-breakpoint-down(md) {
        bottom: -75px;
    }
    &:hover {
        color: $white;
        background-color: $grey-7;
    }
}

.news_load_more_btn {
    background-color: $primary;
    color: $white;
    width: 100%;
    padding: 13px 15px;
    max-width: 150px;
    text-align: center;
    border-radius: 25px;
    margin: 0 auto;
    font-size: 10px;
    text-transform: uppercase;
    &:hover {
        color: $white;
        background-color: $grey-7;
    }
}

.page-template-dealers {
    #filter-data {
        top: 0;
    }
}

.filter_box_price {
    display: flex;
    align-items: center;
}

.filter_box_price {
    input {
        margin: 0px 5px;
        &:first-child {
            margin-left: 0;
        }
        &:last-child {
            margin-right: 0;
        }
    }
}
