/*No Result*/
.no-results {
    max-width: 600px;
    margin: 0 auto;
    padding: 0 0 80px 0;
    text-align: center;
    .page-header {
        h1 {
            color: $primary;
        }
    }
    .page-content {
        color: $accent;
    }
    .search-form {
        .input-group {
            .form-control {
                border: 1px solid $primary;
                border-radius: 30px 0 0 30px;
                padding: 10px 20px;
                height: 58px;
            }
            .input-group-append {
            }
            .submit {
                min-width: 100px;
            }
        }
    }
}

.search-results {
    .content-area {
        margin-bottom: 30px;
    }
    h2.entry-title {
        a {
            color: $accent;
            &:hover,
            &:focus {
                color: $secondary;
            }
        }
    }
    article {
        margin-bottom: 20px;
        padding-bottom: 10px;
        border-bottom: 1px solid rgba($primary, 0.2);
        .btn {
            margin-top: 15px;
            border: none;
            background-color: $primary;
            color: $white;
            &:hover,
            &:focus {
                color: $white;
                background-color: $primary;
            }
        }
        a:not(.btn) {
            color: $primary;
            &:hover,
            &:focus {
                color: $secondary;
                text-decoration: none;
            }
        }
        &:last-child {
            border: none;
        }
        footer {
            display: none;
        }
    }
}
