.breadcrumb-wraper {
    padding: 50px 0 0;
    font-size: 1.2rem;
    .breadcrumb {
        padding: 0;
        margin: 0;
        background-color: $white;
    }
    .breadcrumb-item + .breadcrumb-item::before {
        content: ">";
    }
    .breadcrumb-item {
        color: $primary;
        a {
            color: $black;
        }
    }
}
