//Styles for full width wysiwyg flexible content layout
%ul-listing {
    margin: 0px 0 1rem;
    padding: 0px;
    text-align: left;
    li {
        list-style: none;
        &:before {
            content: "\f101";
            font-family: FontAwesome;
            margin-right: 5px;
        }
    }
}
%ol-listing {
    margin: 0px 0 1rem;
    padding: 0px;
    text-align: left;
    li {
        list-style-position: inside;
    }
}
%blockquote {
    font-size: 1.2rem;
    font-style: italic;
    margin: 15px 0;
    padding-left: 20px;
    color: $primary;
    display: inline-block;
    position: relative;
    border-left: 2px solid $primary;
    text-align: left;
    p {
        &:last-of-type {
            margin-bottom: 0;
        }
    }
}

.default-content {
    font-size: 1.6rem;
    line-height: 1.5;
    ul {
        @extend %ul-listing;
    }
    ol {
        @extend %ol-listing;
    }
    a:not(.btn) {
        color: $primary;
        text-decoration: underline;
        &:hover {
            color: $secondary;
        }
    }
    blockquote {
        @extend %blockquote;
    }
    .btn {
        margin-top: 30px;
    }
    h1,
    h2,
    h3,
    h4,
    h5 {
        color: $black;
        margin-bottom: 30px;
    }
    &.content-lg {
        font-size: 2rem;
        line-height: 1.5;
        @include media-breakpoint-down(lg) {
            font-size: 1.8rem;
        }
        @include media-breakpoint-down(md) {
            font-size: 1.6rem;
        }
    }
    &.content-md {
        font-size: 1.8rem;
        line-height: 1.5;
        @include media-breakpoint-down(lg) {
            font-size: 1.6rem;
        }
    }
    &.content-sm {
        font-size: 1.2rem;
        line-height: 1.5;
    }
}

.full-width-wysiwyg {
    margin: 40px 0;
    h1,
    h2,
    h3,
    h4,
    h5 {
        color: $black;
        margin-bottom: 20px;
        span {
            color: $primary;
        }
    }
    ul {
        @extend %ul-listing;
    }
    ol {
        @extend %ol-listing;
    }
    a:not(.btn) {
        text-decoration: underline;
        &:hover {
        }
    }
    blockquote {
        @extend %blockquote;
    }
    .btn {
        margin-top: 20px;
    }

    .default-content {
        margin-top: 20px;
    }
}

.page-template-listing {
    .full-width-wysiwyg {
        margin-bottom: 60px;
        @include media-breakpoint-down(sm) {
            margin-bottom: 40px;
        }
    }
}
