.brand-logo-slider-section {
    padding: 20px 0;
    .brand-logo-slider {
        // margin-left: -15px;
        // margin-right: -15px;
        display: flex;
        flex-wrap: nowrap;
        padding-bottom: 40px;
        .logo-slide {
            padding-left: 15px;
            padding-right: 15px;
            flex: 0 0 auto;
            width: 178px;
            max-width: 178px;
            a {
                color: $black;
            }
            h5 {
                text-align: center;
                margin: 26px 0 0;
            }
        }
    }
    .logo-box {
        background-color: $grey-1;
        border-radius: 17px;
        padding: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100px;
        @include transition(all 0.3s ease-in-out);
        box-shadow: 0px 3px 6px rgba($black, 0.16);
        @include media-breakpoint-down(lg) {
            padding: 15px;
        }
        img {
            max-width: 100%;
            max-height: 100%;
            width: auto;
        }
        &:hover {
            background-color: $grey-6;
        }
    }
}
