//header styles go here
.main-navigation {
    position: relative;
    width: 100%;
    z-index: 1000;
    background-color: $white;
    .navbar {
        padding: 25px 0;
        box-shadow: 0px 3px 6px rgba($black, 0.16);
        @include transition(all 0.3s ease-in-out);
        @include media-breakpoint-down(lg) {
            padding: 20px 15px;
        }
        .navbar-brand {
            padding-top: 0;
            padding-bottom: 0;
            margin-right: 80px;
            @include media-breakpoint-down(md) {
                margin-right: 20px;
            }
            img {
                height: auto;
                width: 230px;
                @include media-breakpoint-down(md) {
                    width: 200px;
                }
            }
        }
        .navbar-nav {
            .menu-item {
                padding: 0 10px;
                @include media-breakpoint-up(xl) {
                    &:first-child {
                        padding-left: 0;
                    }
                    &:last-child {
                        padding-right: 0;
                    }
                }
                @include media-breakpoint-down(lg) {
                    padding: 0;
                }
                .nav-link {
                    color: $black;
                    font-size: 1.5rem;
                    font-weight: $semi-bold;
                    padding: 18px 10px 12px;
                    position: relative;
                    cursor: pointer;
                    @include media-breakpoint-down(xl) {
                        font-size: 1.4rem;
                    }
                    @include media-breakpoint-down(lg) {
                        padding: 12px 30px;
                        color: $white;
                    }
                    &:hover {
                        color: $primary;
                        @include media-breakpoint-down(lg) {
                            color: rgba($white, 0.7);
                        }
                    }
                }
                .dropdown-menu {
                    visibility: hidden;
                    border-radius: 0;
                    border: none;
                    padding: 0;
                    background-color: $primary;
                    padding: 10px 0;
                    @include media-breakpoint-up(xl) {
                        min-width: 240px;
                        margin-top: 0;
                        border-top: none;
                    }
                    @include media-breakpoint-down(lg) {
                        position: relative;
                        padding: 0;
                        background-color: rgba($grey-1, 0.2);
                    }
                    li {
                        position: relative;
                        padding: 8px 25px;
                        border-bottom: 1px solid rgba($grey-1, 0.3);
                        @include media-breakpoint-up(xl) {
                            &:last-child {
                                border-bottom: 0;
                            }
                        }
                        @include media-breakpoint-down(lg) {
                            padding: 10px 0 10px 40px;
                        }
                        .dropdown-item {
                            color: $white;
                            padding: 0;
                            font-size: 1.4rem;
                            font-weight: $semi-bold;
                            &:before {
                                display: block;
                                content: attr(title);
                                font-weight: bold;
                                height: 0;
                                overflow: hidden;
                                visibility: hidden;
                            }
                        }
                        .dropdown-menu {
                            @include media-breakpoint-up(xl) {
                                visibility: hidden !important;
                                left: 100%;
                                top: 0;
                            }
                            @include media-breakpoint-down(lg) {
                                padding: 0;
                                li {
                                    .dropdown-item {
                                        padding: 10px 0 10px 50px;
                                    }
                                }
                            }
                        }
                        &:hover {
                            .dropdown-item {
                                color: $grey-1;
                                background-color: transparent;
                            }
                            @include media-breakpoint-up(xl) {
                                .dropdown-menu {
                                    visibility: visible !important;
                                    display: block;
                                    top: 0;
                                    padding: 0;
                                }
                            }
                        }
                        &.active {
                            .dropdown-item {
                                color: $grey-1;
                                background-color: transparent;
                            }
                        }
                    }
                    .menu-item-has-children {
                        &:after {
                            margin-left: 0.255em;
                            vertical-align: 0.255em;
                            content: "\f105";
                            position: absolute;
                            right: 10px;
                            top: 10px;
                            color: rgba($white, 0.5);
                            font-family: "FontAwesome";
                            @include media-breakpoint-down(lg) {
                                display: none;
                            }
                        }
                    }
                    &.show {
                        display: block;
                        visibility: visible;
                    }
                    &.drop-open {
                        display: block;
                        visibility: visible;
                    }
                }
                .dropdown-toggle {
                    &::before {
                        content: "";
                        position: absolute;
                        left: 50%;
                        margin-left: -22px;
                        margin-top: -15px;
                        width: 0;
                        z-index: 1;
                        height: 0;
                        border-bottom: solid 12px $primary;
                        border-left: solid 13px transparent;
                        border-right: solid 13px transparent;
                        bottom: 0;
                        opacity: 0;
                        visibility: hidden;
                        @include media-breakpoint-down(lg) {
                            content: none;
                        }
                    }
                    &:after {
                        content: "\f107";
                        font-family: fontawesome;
                        border: none;
                        vertical-align: top;
                        margin: -2px 0 0 7px;
                        font-size: 1.8rem;
                        @include media-breakpoint-down(lg) {
                            display: none;
                        }
                    }
                }
                &.active {
                    .nav-link {
                        color: $primary;
                    }
                }
                &:hover {
                    .nav-link {
                        &:hover {
                            color: $primary;
                        }
                    }
                    .dropdown-menu {
                        visibility: visible;
                        @include media-breakpoint-up(xl) {
                            display: block;
                            top: 100%;
                        }
                    }
                    .dropdown-toggle {
                        &:after {
                            content: "\f106";
                        }
                        &::before {
                            opacity: 1;
                            visibility: visible;
                        }
                    }
                }
            }
            .menu-item-has-children {
                .arrow-icon {
                    display: none;
                    @include media-breakpoint-down(lg) {
                        display: block;
                        position: absolute;
                        right: 5px;
                        top: 8px;
                        width: 30px;
                        height: 30px;
                    }
                    &:after {
                        content: "";
                        border-top: 0.32em solid $white;
                        border-right: 0.32em solid transparent;
                        border-bottom: 0;
                        border-left: 0.32em solid transparent;
                        top: 50%;
                        transform: translateY(-50%);
                        left: 10px;
                        position: absolute;
                    }
                }
            }
        }
    }
    &.stickyHead {
        position: fixed;
        width: 100%;
        top: 0;
        .navbar {
            padding: 15px;
        }
    }
    @include media-breakpoint-up(md) {
        .navbar-expand-xl .navbar-collapse {
            justify-content: flex-end;
        }
    }
    .lang-menu {
        list-style: none;
        display: flex;
        padding: 0;
        margin: 6px 0 0;
        ul {
            list-style: none;
            display: flex;
            padding: 0;
            margin: 0 0 0 20px;
            @include media-breakpoint-down(md) {
                margin: 0;
            }
            li {
                &:last-child {
                    a {
                        margin-right: 0;
                        padding-right: 0;
                        &:after {
                            display: none;
                        }
                    }
                }
                a {
                    color: $black;
                    text-transform: uppercase;
                    font-size: 1.5rem;
                    font-weight: $semi-bold;
                    padding: 18px 0 12px;
                    position: relative;
                    margin: 0 10px;
                    &:hover {
                        color: $primary;
                    }
                    &:after {
                        content: "|";
                        position: absolute;
                        left: 30px;
                        top: 16px;
                        color: $accent;
                    }
                }
            }
        }
    }
}

.navbar-toggler {
    padding: 0;
    line-height: 1;
    border: 0;
    border-radius: 0;
    @include transition(all 0.3s ease-in-out);
    @include media-breakpoint-up(xl) {
        display: none;
    }
    @include media-breakpoint-down(lg) {
        padding-left: 10px;
    }
    &:focus {
        box-shadow: none;
        outline: 0;
    }
    .hamburger {
        z-index: 1000;
        position: relative;
        .line {
            width: 35px;
            height: 3px;
            background-color: $black;
            display: block;
            margin: 8px auto;
            transition: all 0.3s ease-in-out;
            @include media-breakpoint-down(lg) {
                width: 20px;
                margin: 4px auto;
            }
        }
    }
}

body.offcanvas-active {
    overflow: hidden;
    .navbar-toggler {
        .hamburger {
            .line {
                background-color: $black;
                &:nth-child(1) {
                    transform: translateY(16px) rotate(45deg);
                    @include media-breakpoint-down(lg) {
                        transform: translateY(7px) rotate(45deg);
                    }
                }
                &:nth-child(2) {
                    opacity: 0;
                }
                &:nth-child(3) {
                    transform: translateY(-6px) rotate(-45deg);
                    @include media-breakpoint-down(lg) {
                        transform: translateY(-7px) rotate(-45deg);
                    }
                }
            }
        }
    }
}
.screen-overlay {
    @include media-breakpoint-down(lg) {
        width: 0%;
        height: 100%;
        z-index: 999;
        position: fixed;
        top: 99px;
        left: 0;
        opacity: 0;
        visibility: hidden;
        background-color: rgba($black, 0.6);
        transition: opacity 0.2s linear, visibility 0.1s, width 1s ease-in;
        &.show {
            transition: opacity 0.5s ease, width 0s;
            opacity: 1;
            width: 100%;
            visibility: visible;
        }
    }
    @include media-breakpoint-down(md) {
        top: 79px;
    }
}

.mobile-offcanvas {
    @include media-breakpoint-down(lg) {
        visibility: hidden;
        transform: translateX(100%);
        border-radius: 0;
        //display: block;
        position: fixed;
        top: 99px;
        right: 0;
        height: 100%;
        z-index: 1200;
        width: 80%;
        overflow-y: auto;
        overflow-x: hidden;
        transition: visibility 0.2s ease-in-out, transform 0.2s ease-in-out;
        background-color: $primary;
        padding: 15px 0;
        &.show {
            visibility: visible;
            transform: translateX(0);
        }
        .dropdown-menu {
            border: 0;
        }
        &.collapse:not(.show) {
            display: block;
        }
    }
    @include media-breakpoint-down(md) {
        top: 79px;
    }
}

html[lang="en-US"] .lang-menu ul li:last-child a {
    color: $primary;
}

html[lang="fr-FR"] .lang-menu ul li:first-child a {
    color: $primary;
}
