.dealers-details-section {
    padding: 60px 0 90px;
    @include media-breakpoint-down(sm) {
        padding: 40px 0 40px;
    }
    .contact-details {
        display: flex;
        margin-bottom: 15px;
        h5 {
            margin-bottom: 10px;
        }
        p {
            font-size: 1.6rem;
            max-width: 188px;
        }
        a {
            color: $black;
        }
        i {
            font-size: 32px;
            color: $primary;
            margin-right: 20px;
            flex: 0 0 10%;
            max-width: 10%;
        }
    }
    .external-links {
        display: flex;
        align-items: center;
        text-align: center;
        @include media-breakpoint-down(sm) {
            margin-bottom: 25px;
        }
        a {
            color: $black;
            font-size: 12px;
        }
        i {
            font-size: 16px;
            color: $primary;
            margin-right: 5px;
        }
    }
    .title-wrap {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 40px;
        h2 {
            margin-bottom: 0;
            span {
                margin-top: 10px;
            }
        }
        img {
            padding: 5px;
            max-width: 130px;
            border: 1px solid #ddd;
            border-radius: 3px;
        }
        @include media-breakpoint-down(sm) {
            display: block;
            h2 {
                margin-bottom: 15px;
            }
        }
    }

    .dealers-siderbar {
        padding-bottom: 80px;
        @include media-breakpoint-down(md) {
            padding-bottom: 0;
        }
        @include media-breakpoint-down(sm) {
            padding-bottom: 0;
            padding-top: 40px;
        }
        .brand-details {
            box-shadow: 0px 3px 6px rgba($black, 0.16);
            max-width: 216px;
            text-align: center;
            margin-top: 50px;
            margin-bottom: 50px;
            a {
                padding: 30px;
                display: block;
            }
        }
        .contact-details {
            display: flex;
            margin-bottom: 40px;
            h5 {
                margin-bottom: 12px;
            }
            p {
                font-size: 1.2rem;
                max-width: 188px;
            }
            a {
                color: $black;
            }
            i {
                font-size: 38px;
                color: $primary;
                margin-right: 25px;
                flex: 0 0 10%;
                max-width: 10%;
            }
        }
        .gravityform-car-page {
            background-color: $grey-1;
            box-shadow: 0px 3px 6px rgba($black, 0.16);
            padding: 25px 20px 10px;
            h2 {
                text-align: center;
                margin-bottom: 30px;
                font-size: 2rem;
            }
            .gform_footer {
                text-align: center;
            }
        }
    }

    .dealers-details {
        padding-top: 50px;
        @include media-breakpoint-down(sm) {
            padding-top: 30px;
        }
        .nav-tabs {
            .nav-link {
                padding: 15px 23px 15px;
                font-size: 12px;
                line-height: 22px;
                font-weight: 700;
                text-transform: uppercase;
                i {
                    margin-right: 8px;
                    font-size: 14px;
                }
            }
        }
        .tab-content {
            padding-top: 30px;
            h4 {
                margin-bottom: 20px;
            }
            .image-card {
                margin-bottom: 31px;
                height: auto;
                .image-card-detail {
                    .card-right-detail {
                        padding-left: 10px;
                    }
                }
            }
        }
    }
}
