.listing-details-section {
    padding: 40px 0 40px;
    @include media-breakpoint-down(md) {
        padding: 40px 0 0;
    }
    .seller-rating-wrap {
        word-break: break-all;
    }
    .listing-left-wraper {
        max-width: 90%;
        @include media-breakpoint-down(lg) {
            max-width: 100%;
        }
    }
    .title-wrap {
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include media-breakpoint-down(sm) {
            display: block;
        }
        h1 {
            max-width: 635px;
            @include media-breakpoint-down(md) {
                max-width: 470px;
            }
            @include media-breakpoint-down(sm) {
                margin-bottom: 25px;
            }
        }
        .price-label {
            font-size: 2.75rem;
            padding: 19px 32px;
            font-weight: $semi-bold;
            background-color: $primary;
            color: $white;
            @include media-breakpoint-down(sm) {
                display: inline-block;
                text-align: center;
                font-size: 2rem;
                padding: 15px 25px;
            }
        }
    }
    .listing-slider {
        margin-top: 50px;
        margin-bottom: 50px;
        @include media-breakpoint-down(sm) {
            margin-top: 30px;
            margin-bottom: 30px;
        }
        .slider-single {
            img {
                width: 100%;
                height: 505px;
                object-fit: cover;
                @include media-breakpoint-down(md) {
                    height: 400px;
                }
                @include media-breakpoint-down(sm) {
                    height: 200px;
                }
            }
        }
        .slider-nav {
            margin-top: 5px;
            .slick-next:before,
            .slick-prev:before {
                color: $primary;
                opacity: 1;
            }
            .slick-next {
                @include media-breakpoint-down(sm) {
                    right: 0;
                    z-index: 1;
                    background-color: $white;
                }
                &:before {
                    content: "\f105";
                    font-family: FontAwesome;
                }
            }
            .slick-prev {
                @include media-breakpoint-down(sm) {
                    left: 0;
                    z-index: 1;
                    background-color: $white;
                }
                &:before {
                    content: "\f104";
                    font-family: FontAwesome;
                }
            }
            .slick-list {
                margin: 0 -5px;
            }
            .slick-slide {
                cursor: pointer;
                margin: 0 6px;
            }
            img {
                width: 100%;
                height: 140px;
                object-fit: cover;
                @include media-breakpoint-down(md) {
                    height: 90px;
                }
                @include media-breakpoint-down(sm) {
                    height: 70px;
                }
            }
        }
    }
    .details-voice-wrap {
        margin-bottom: 60px;
        @include media-breakpoint-down(sm) {
            margin-bottom: 30px;
        }
        h4 {
            font-size: 2rem;
            margin-bottom: 40px;
        }
        .details-voices {
            display: flex;
            justify-content: space-between;
            font-size: 12px;
            margin-bottom: 20px;
            .icon-text {
                display: flex;
                align-items: center;
                img {
                    width: 18px;
                    margin-right: 13px;
                    object-fit: contain;
                }
                i {
                    color: $primary;
                    font-size: 20px;
                    margin-right: 13px;
                }
            }
            .details-text {
                text-align: left;
                flex: 0 0 35%;
                max-width: 35%;
                strong {
                    display: block;
                }
            }
        }
    }
    .characterists-wrap {
        margin-bottom: 60px;
        @include media-breakpoint-down(sm) {
            margin-bottom: 30px;
        }
        h4 {
            font-size: 2rem;
            margin-bottom: 40px;
        }
        .characterGrid {
            display: flex;
            flex-wrap: wrap;
            .charac-block {
                display: flex;
                width: 25%;
                flex-grow: 1;
                font-size: 12px;
                margin-bottom: 15px;
                align-items: flex-start;
                .charac-text {
                    margin-right: 10px;
                }
                @include media-breakpoint-down(sm) {
                    width: 50%;
                }
                .icon {
                    display: flex;
                    align-items: center;
                    flex: 0 0 25px;
                    margin-top: 3px;
                    img {
                        width: 20px;
                        margin-right: 12px;
                        object-fit: contain;
                    }
                }
            }
        }
    }
    .seller-rating-wrap {
        h4 {
            font-size: 2rem;
            margin-bottom: 40px;
        }
        p {
            font-size: 12px;
        }
    }
    .listing-siderbar {
        @include media-breakpoint-down(md) {
            margin-top: 15px;
        }
        .brand-details {
            box-shadow: 0px 3px 6px rgba($black, 0.16);
            max-width: 216px;
            text-align: center;
            margin-top: 50px;
            margin-bottom: 50px;
            a {
                padding: 30px;
                display: block;
            }
        }
        .contact-details {
            display: flex;
            margin-bottom: 25px;
            h5 {
                margin-bottom: 12px;
            }
            p {
                font-size: 1.2rem;
                max-width: 188px;
            }
            a {
                color: $black;
            }
            i {
                font-size: 38px;
                color: $primary;
                margin-right: 15px;
                flex: 0 0 30px;
                max-width: 30px;
            }
            .dealership_mobile_number {
                font-size: 12px;
            }
        }
        .gravityform-car-page {
            background-color: $grey-1;
            box-shadow: 0px 3px 6px rgba($black, 0.16);
            padding: 25px 20px 10px;
            h2 {
                text-align: center;
                margin-bottom: 30px;
                font-size: 2rem;
            }
            .gform_footer {
                text-align: center;
            }
            .gform_wrapper.gravity-theme .gfield-choice-input {
                margin-top: 5px;
                top: 0;
                vertical-align: top;
            }
        }
    }
}

.related-product-section {
    margin: 0 0 60px;
    @include media-breakpoint-down(md) {
        margin: 0 0 40px;
    }
    @include media-breakpoint-down(sm) {
        margin: 50px 0 40px;
    }
    .related-product-slider {
        margin-left: -10px;
        margin-right: -10px;
        border-top: 1px solid;
        padding-top: 60px;
        .slick-slide {
            padding-left: 10px;
            padding-right: 10px;
            margin-bottom: 0;
            height: auto;
        }
        .slick-track {
            display: flex;
        }
    }
    .image-card {
        margin-bottom: 0;
        height: calc(100% - 5px);
        figure {
            height: 223px;
        }
        .image-card-detail {
            padding: 20px 20px 25px;
            .product-price {
                justify-content: flex-end;
            }
            .image-card-detail-row {
                display: block;
                .card-left-detail {
                    max-width: 100%;
                    flex: 0 0 100%;
                    h5 {
                        max-width: 100%;
                    }
                }
                .card-right-detail {
                    flex: 0 0 100%;
                    max-width: 100%;
                    padding-left: 0;
                    flex-wrap: inherit;
                    width: 100%;
                    margin-bottom: 25px;
                    display: flex;
                    .card-detail-list {
                        display: flex;
                        align-items: center;
                        margin-bottom: 0;
                        margin-right: 20px;
                    }
                }
            }
        }
    }
}
