/*404 Error Page START*/
.error-page{
    text-align: center;
    padding: 80px 0;
    @include media-breakpoint-down(sm) {
        padding: 50px 0;
    }
    h2{
        font-size: 66px;
        color: $primary;
        @include media-breakpoint-down(sm) {
            font-size: 42px;
        }
    }
    h3{
        font-size: 40px;
        margin-bottom: 0;
        color: $accent;
        @include media-breakpoint-down(sm) {
            font-size: 28px;
        }
        @include media-breakpoint-down(xs) {
            font-size: 24px;
        }
    }
    .btn {
        background-color: $primary;
        color: $white;
        padding: 15px 25px;
        font-weight: 600;
        margin-top: 30px;
        display: inline-block;
        &:hover {
            background-color: $grey-7;
        }
    }
    p{
        font-size: 20px;
    }
}
/*404 Error Page END*/