/*
* Use existing Bootstrap 4 classes and
* variables to extend - override CF7 style
*
* Useful CF7 classes:
* .wpcf7 the wrapper element
* .wpcf7-form
* .wpcf7-form-control
* .wpcf7-text
* .wpcf7-email
* .wpcf7-textarea
* .wpcf7-submit
*/
.wpcf7 {
	max-width: 600px;
	margin: 0 auto !important; }
	.wpcf7 .wpcf7-form label {
	width: 100%; }
	.wpcf7 .wpcf7-not-valid-tip {
	color: #dc3545; }
	.wpcf7 .wpcf7-validation-errors {
	color: #dc3545;
