//footer styles go here
footer {
    background-color: $white;
    border-top: 0.5px solid #95989a;
    padding: 27px 0;
    .footer-content {
        .copyright-text {
            font-size: 1.2rem;
            @include media-breakpoint-down(sm) {
                text-align: center;
            }
        }
    }
    .footer-user {
        text-align: right;
        @include media-breakpoint-down(sm) {
            text-align: center;
        }
        a {
            color: $black;
            font-size: 2rem;
            &:hover {
                color: $primary;
            }
        }
    }
    .footer-social {
        ul {
            display: flex;
            align-items: center;
            list-style: none;
            margin: 0;
            padding: 0;
            justify-content: center;
            li {
                margin: 0 7px;
                &:first-child {
                    margin-left: 0;
                }
                &:last-child {
                    margin-right: 0;
                }
                a {
                    color: $black;
                    font-size: 1.8rem;
                    &:hover {
                        color: $primary;
                    }
                }
            }
        }
    }
}
