.filter-wraper {
    .filter-inner {
        box-shadow: 0px 5px 5px rgba($black, 0.16);
        opacity: 1;
        padding: 20px 20px 0;
        margin-bottom: 70px;
        @include media-breakpoint-down(sm) {
            margin-bottom: 50px;
            padding: 20px 20px 20px;
        }
        .form-control {
            font-size: 12px;
            color: $accent;
            height: 42px;
            margin-bottom: 20px;
            border-radius: 7px;
        }
        select {
            border-radius: 7px;
            font-weight: 600;
        }
        .filter-title-wrap {
            background-color: $grey-7;
            border-radius: 7px;
            color: $white;
            padding: 15px;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-bottom: 15px;
            h5 {
                margin-bottom: 0;
                margin-left: 15px;
            }
        }
        .btn {
            background-color: $primary;
            color: $white;
            width: 100%;
            padding: 11px 15px;
            font-weight: 600;
            &:hover {
                background-color: $grey-7;
            }
        }
    }
}

.news-tabs-wrap {
    padding-bottom: 60px;
    .nav-pills .nav-link.active,
    .nav-pills .show > .nav-link {
        color: $accent;
        background-color: $white;
        font-weight: 600;
        font-size: 2rem;
        @include media-breakpoint-down(sm) {
            font-size: 1.5rem;
        }
    }
    .nav-pills .nav-link {
        color: $grey-6;
        background-color: $white;
        font-weight: 600;
        font-size: 2rem;
        padding-left: 0;
        padding-right: 45px;
        @include media-breakpoint-down(sm) {
            padding-right: 15px;
            font-size: 1.5rem;
        }
    }
    ul {
        border-bottom: 1px solid $grey-6;
        padding-bottom: 20px;
        margin-bottom: 45px;
    }
    .image-card {
        .image-card-detail {
            padding: 20px 20px 18px;
            h5 {
                font-size: 1.5rem;
            }
        }
    }
}

.blog {
    .recent-videos-section {
        background-color: $white;
        margin: 0;
        padding: 30px 0 100px;
        @include media-breakpoint-down(sm) {
            padding: 20px 0 80px;
        }
    }
}
