.contact-main-wrap {
    margin: 60px 0;
    h1 {
        margin-bottom: 60px;
        @include media-breakpoint-down(md) {
            margin-bottom: 30px;
        }
    }
    .contact-inner {
        background-color: $grey-1;
        box-shadow: 0px 3px 6px rgba($black, 0.16);
        padding: 60px 85px 70px;
        @include media-breakpoint-down(sm) {
            padding: 30px;
        }
        .contact-form {
            max-width: 560px;
            input {
                height: 50px;
            }
            textarea {
                height: auto;
            }
            .gfield-choice-input {
                height: auto;
            }
            input,
            textarea {
                font-weight: 500;
                font-size: 12px !important;
            }
            .gfield-choice-input + label {
                font-size: 12px !important;
            }
            .ginput_container {
                margin-bottom: 10px;
            }
        }
    }
    .contact-social {
        margin-top: 100px;
        @include media-breakpoint-down(md) {
            margin-top: 30px;
        }
        h4 {
            font-size: 2rem;
            margin-bottom: 30px;
        }
        ul {
            display: flex;
            align-items: center;
            list-style: none;
            margin: 0;
            padding: 0;
            li {
                margin: 0 18px;
                &:first-child {
                    margin-left: 0;
                }
                &:last-child {
                    margin-right: 0;
                }
                a {
                    color: $black;
                    font-size: 1.8rem;
                    &:hover {
                        color: $primary;
                    }
                }
            }
        }
    }
    .contact-details {
        margin-top: 100px;
        @include media-breakpoint-down(md) {
            margin-top: 30px;
        }
        h4 {
            font-size: 2rem;
            margin-bottom: 35px;
        }
        ul {
            font-weight: 600;
            margin: 0;
            padding: 0;
            list-style: none;
            font-size: 12px;
            li {
                margin-bottom: 20px;
                position: relative;
                padding-left: 20px;
                &::before {
                    content: url("../images/power-icon.png");
                    position: absolute;
                    top: 2px;
                    left: 0;
                }
            }
        }
    }
}
