.card-content-row {
    margin-bottom: -30px;
    .card-content-col {
        margin-bottom: 30px;
    }
}
.image-card {
    display: flex;
    flex-direction: column;
    height: 100%;
    position: relative;
    box-shadow: 0px 3px 6px rgba($black, 0.16);
    @include transition(all 0.3s ease-in-out);
    @at-root .round-icon#{&} {
        box-shadow: none;
    }
    figure {
        margin-bottom: 0;
        overflow: hidden;
        height: 235px;
        position: relative;
        @at-root .round-icon#{&} {
            height: auto;
            overflow: inherit;
            margin-bottom: -90px;
            @include media-breakpoint-down(lg) {
                margin-bottom: -75px;
            }
        }
        .image {
            height: 100%;
            @at-root .round-icon#{&} {
                height: 180px;
                width: 180px;
                background-color: $white;
                border-radius: 50%;
                display: flex;
                align-items: center;
                justify-content: center;
                margin: 0 auto;
                padding: 20px;
                @include transition(all 0.3s ease-in-out);
                @include media-breakpoint-down(lg) {
                    height: 150px;
                    width: 150px;
                }
            }
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
            @include transition(all 0.3s ease-in-out);
            @at-root .round-icon#{&} {
                height: auto;
                width: 80%;
                object-fit: inherit;
                max-height: 100%;
            }
        }
        .view-icon {
            width: 53px;
            height: 36px;
            background-color: rgba($white, 0.5);
            display: flex;
            align-items: center;
            justify-content: center;
            position: absolute;
            top: 10px;
            right: 10px;
            z-index: 1;
            cursor: pointer;
            i {
                color: $black;
                font-size: 1.8rem;
            }
        }
        .play-button {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            cursor: pointer;
            z-index: 1;
            width: 150px;
            height: 98px;
            display: block;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: rgba($grey-6, 0.8);
            border-radius: 17px;
            @include media-breakpoint-down(lg) {
                width: 130px;
                height: 85px;
            }
            .play-icon {
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 30px 0 30px 50px;
                border-color: transparent transparent transparent $primary;
                @include media-breakpoint-down(lg) {
                    border-width: 25px 0 25px 40px;
                }
            }
        }
    }
    .image-card-detail {
        padding: 20px;
        background-color: $white;
        flex: 1;
        @media only screen and (min-width: 1200px) and (max-width: 1400px) {
            padding: 15px;
        }
        @at-root .round-icon#{&} {
            background-color: $grey-6;
            border-radius: 17px;
            box-shadow: 0px 3px 6px rgba($black, 0.16);
            padding: 110px 50px 40px;
            display: flex;
            flex: 1;
            align-items: flex-start;
            @include media-breakpoint-down(lg) {
                padding: 90px 30px 30px;
            }
        }
        p {
            @at-root .round-icon#{&} {
                text-align: center;
                font-weight: $semi-bold;
                &:last-of-type {
                    margin-bottom: 0;
                }
                span {
                    color: $primary;
                }
            }
        }
        h5 {
            margin-bottom: 20px;
            width: 100%;
            @media only screen and (min-width: 1200px) and (max-width: 1400px) {
                font-size: 1.5rem;
            }
            @at-root .video-card#{&} {
                margin-bottom: 0;
                text-align: center;
            }
            a {
                color: $black;
                text-decoration: none;
                &:hover {
                    color: $primary;
                }
            }
        }
        a {
            text-decoration: underline;
            font-weight: $semi-bold;
        }
        .default-content {
            margin-bottom: 1.6rem;
        }
        .image-card-detail-row {
            display: flex;
            height: 100%;
        }
        .card-left-detail {
            flex: 0 0 65%;
            max-width: 65%;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            @media only screen and (min-width: 1200px) and (max-width: 1400px) {
                flex: 0 0 60%;
                max-width: 60%;
            }
        }
        .product-price {
            display: flex;
            align-items: flex-end;
            justify-content: flex-end;
            .price-label {
                font-size: 1.4rem;
                padding: 10px 12px;
                font-weight: $semi-bold;
                @include media-breakpoint-down(lg) {
                    font-size: 1.2rem;
                }
                @media only screen and (min-width: 1200px) and (max-width: 1400px) {
                    font-size: 1.2rem;
                    padding: 5px 5px;
                }
                &.old-price {
                    background-color: $grey-1;
                    color: $black;
                    text-decoration: line-through;
                }
                &.new-price {
                    background-color: $primary;
                    color: $white;
                }
            }
        }
        .card-right-detail {
            flex: 0 0 35%;
            max-width: 35%;
            padding-left: 20px;
            @media only screen and (min-width: 1200px) and (max-width: 1400px) {
                flex: 0 0 40%;
                max-width: 40%;
                padding-left: 5px;
            }
            .card-detail-list {
                display: flex;
                align-items: center;
                margin-bottom: 20px;
                &:last-child {
                    margin-bottom: 0;
                }
                .card-detail-icon {
                    margin-right: 14px;
                    width: 27px;
                    min-width: 27px;
                    @media only screen and (min-width: 1200px) and (max-width: 1400px) {
                        margin-right: 7px;
                        width: 15px;
                        min-width: 15px;
                    }
                    img {
                        height: 24px;
                        vertical-align: middle;
                        object-fit: contain;
                    }
                }
                .card-detail-label {
                    p {
                        font-size: 1.2rem;
                        margin: 0;
                        font-weight: $regular;
                    }
                }
            }
        }
    }
    &:hover {
        figure {
            .image {
                @at-root .round-icon#{&} {
                    box-shadow: 0px 3px 6px rgba($black, 0.16);
                }
            }
            img {
                transform: scale(1.1);
            }
        }
        .image-card-detail {
            border-color: $primary;
        }
    }
}
