.dealers-main-wrap {
    @include media-breakpoint-down(sm) {
        padding-bottom: 40px;
    }
    .image-card {
        .image-card-detail {
            padding: 15px 20px 5px;
            min-height: 223px;
            .image-card-detail-row {
                span {
                    margin-bottom: 10px;
                }
                .card-left-detail {
                    margin-bottom: 10px;
                }
                .card-right-detail {
                    margin-bottom: 15px;
                    display: flex;
                    a {
                        text-decoration: underline;
                        font-size: 12px;
                        color: $accent;
                    }
                    .card-detail-list {
                        display: flex;
                        align-items: center;
                        margin-bottom: 0;
                        margin-right: 10px;
                        width: 50%;
                        &.map-list {
                            width: 90%;
                        }
                        .card-detail-label {
                            span {
                                color: $primary;
                            }
                        }
                        .card-detail-icon {
                            margin-right: 8px;
                            width: auto;
                            min-width: 15px;
                            i {
                                color: $primary;
                            }
                        }
                    }
                }
            }
            h5 {
                margin-bottom: 0;
            }
        }
        figure {
            height: 150px;
            border-bottom: 1px solid #ccc;
            img {
                object-fit: contain;
            }
        }
        .rating {
            .dealer-rating {
                .stm-rate-sum {
                    display: inline-block;
                    max-width: 100%;
                    font-size: 12px;
                    color: #888888;
                }
                .stm-rate-unit {
                    position: relative;
                    top: 4px;
                    display: inline-block;
                    margin-right: 3px;
                    max-width: 100%;
                    vertical-align: top;
                    .stm-rate-inner {
                        position: relative;
                        display: inline-block;
                        vertical-align: top;
                    }
                    .stm-rate-filled {
                        font-size: 13px;
                        overflow: hidden;
                        &::after {
                            color: #edab1f;
                            font-family: "FontAwesome";
                            font-weight: 600;
                            font-style: normal;
                            font-size: inherit;
                            text-rendering: auto;
                            transform: translate(0, 0);
                            content: "\f006 \f006 \f006 \f006 \f006";
                            letter-spacing: 1px;
                        }
                    }
                    .stm-rate-not-filled {
                        position: absolute;
                        top: 0;
                        left: 0;
                        font-size: 13px;
                        &::after {
                            color: #edab1f;
                            font-family: "FontAwesome";
                            font-weight: 400;
                            font-style: normal;
                            font-size: inherit;
                            text-rendering: auto;
                            transform: translate(0, 0);
                            content: "\f006 \f006 \f006 \f006 \f006";
                            letter-spacing: 1px;
                        }
                    }
                }
            }
        }
    }
    .dealers-row {
        .row {
            border-bottom: 1px solid #ccc;
            padding-bottom: 30px;
            margin-bottom: 30px;
            align-items: center;
            @include media-breakpoint-down(sm) {
                padding-bottom: 10px;
                margin-bottom: 35px;
            }
            .image {
                @include media-breakpoint-down(sm) {
                    margin-bottom: 15px;
                }
                img {
                    padding: 10px 15px;
                    border: 1px solid #b9b9b9;
                    border-radius: 5px;
                    width: 100%;
                    max-width: 240px;
                }
            }
            .review-title-wrap {
                @include media-breakpoint-down(sm) {
                    margin-bottom: 15px;
                }
                a {
                    color: $accent;
                }
                i {
                    color: $primary;
                    font-size: 20px;
                    margin-right: 5px;
                    margin-top: 5px;
                    @include media-breakpoint-down(sm) {
                        margin-top: 12px;
                        margin-bottom: 13px;
                    }
                }
            }
            .count-label {
                p {
                    font-weight: 600;
                    margin: 0;
                }
            }
            .card-detail-list {
                display: flex;
                font-size: 14px;
                font-weight: 600;
                @include media-breakpoint-down(sm) {
                    margin-bottom: 15px;
                }
                .card-detail-icon {
                    margin-right: 8px;
                    font-size: 16px;
                }
                .card-detail-label {
                    a {
                        color: $accent;
                    }
                }
            }
            .map-list {
                p {
                    font-size: 12px;
                }
            }
        }
    }
}
