.compare-main-wrap {
    padding: 80px 0 40px;
    .border-title {
        border-bottom: 4px solid $primary;
        margin-bottom: 20px;
        padding-bottom: 20px;
        display: inline-block;
        max-width: 230px;
    }
    .compare-car {
        margin-bottom: 20px;
        cursor: pointer;
        h3 {
            font-size: 1.8rem;
            margin-top: 10px;
        }
        a {
            display: block;
            width: 100%;
            background-color: #f2f2f2;
            box-shadow: 0px 3px 6px rgba($black, 0.16);
            border-radius: 7px;
            height: 160px;
            text-align: center;
            display: flex;
            justify-content: center;
            align-items: center;
            opacity: 1;
            flex-direction: column;
            padding: 0 15px;
            color: $black;
            position: relative;
            &.active {
                &::before {
                    position: absolute;
                    top: 0;
                    left: 0;
                    content: "";
                    background-color: rgba(255, 255, 255, 0.7);
                    width: 100%;
                    height: 100%;
                    border-radius: 7px;
                    z-index: 9;
                }
                .car-img {
                    position: absolute;
                    border-radius: 7px;
                    height: 160px;
                    width: 100%;
                }
            }
            .car-icon-wrap {
                position: relative;
                z-index: 9;
            }
        }
    }
}

.compare-details-section {
    .details-voice-wrap {
        .details-voices {
            font-size: 17px;
            margin-bottom: 30px;
            font-weight: 600;
            min-height: 28px;
            @include media-breakpoint-down(sm) {
                font-size: 12px;
            }
        }
        .details-text {
            color: $grey-4;
            margin-bottom: 30px;
            min-height: 29px;
            @include media-breakpoint-down(sm) {
                font-size: 12px;
            }
        }
    }
}

.page-template-compare {
    /* The Modal (background) */
    .modal {
        display: none;
        /* Hidden by default */
        position: fixed;
        /* Stay in place */
        z-index: 1;
        /* Sit on top */
        padding-top: 22%;
        /* Location of the box */
        left: 0;
        top: 0;
        width: 100%;
        /* Full width */
        height: 100%;
        /* Full height */
        overflow: auto;
        /* Enable scroll if needed */
        background-color: rgb(0, 0, 0);
        /* Fallback color */
        background-color: rgba(0, 0, 0, 0.4);
        /* Black w/ opacity */
        z-index: 9999;
    }

    /* Modal Content */
    .modal-content {
        background-color: #fefefe;
        margin: auto;
        padding: 20px;
        border: 1px solid #888;
        width: 340px;
    }

    .filter_section {
        padding-top: 30px;
    }

    /* The Close Button */
    .close {
        color: #000;
        float: right;
        font-size: 28px;
        font-weight: bold;
        text-align: right;
        position: absolute;
        right: 7px;
        top: 5px;
    }

    .close:hover,
    .close:focus {
        color: #000;
        text-decoration: none;
        cursor: pointer;
    }

    .form-control {
        font-size: 18px;
    }
}
